import { defineComponent } from 'vue';
const IconProps = {
    id: {
        type: String,
        default: ''
    },
    name: {
        type: String,
        default: ''
    },
    click: {
        type: Function,
        default: () => { }
    }
};
const NIcon = defineComponent({
    name: 'NIcon',
    props: IconProps,
    setup(props) {
        const { name, click } = props;
        return () => (<i class={'iconfont ' + name} onClick={click}/>);
    }
});
NIcon.install = function (app) {
    app.component(NIcon.name, NIcon);
    return app;
};
export default NIcon;
