import { defineComponent, toRef } from 'vue';
import classNames from '../../src/utils/className';
import './index.scss';
const BadgeProps = {
    color: {
        type: String,
        default: '#f44336'
    },
    content: {
        type: String,
        default: ''
    }
};
const NBadge = defineComponent({
    name: 'NBadge',
    props: BadgeProps,
    setup(props, { slots }) {
        const content = toRef(props, 'content');
        const classString = classNames([
            'n-badge-common',
            content.value === '' ? 'n-badge-default' : 'n-badge-content'
        ]);
        return () => (<div class="n-badge">
        {slots.default && slots.default()}
        <span class={classString} style={{ backgroundColor: props.color }}>
          {content.value}
        </span>
      </div>);
    }
});
NBadge.install = function (app) {
    app.component(NBadge.name, NBadge);
    return app;
};
export default NBadge;
